$(".account-sidebar__show").click(function () {
    $(this).toggleClass("_active")
    $(this).siblings(".account-sidebar__hidden").slideToggle()
})

$(".favourite-group__top").click(function () {
    $(this).toggleClass("_active")
    $(this).siblings(".favourite-group__bottom").slideToggle()
})


$(".favourite-btn").click(function () {
    $(this).toggleClass("_active")
})

$(".favourite-card .props__btn").click(function () {
    $(this).parents(".favourite-card").toggleClass("_show-props")
    $(this).parents(".favourite-card").find(".props__items").slideToggle()
})

$(".order-card__toggle").click(function () {
    $(this).parents(".order-card").find(".order-card__hidden").slideToggle()
})

$(".company-card__show").click(function () {
    $(this).toggleClass("_active")
    $(this).siblings(".company-card__hidden").slideToggle()
})
